<template>
    
    <div>
        <header>
            <div class="deit_main_span">
        <span class="sn sn-title">快递配送</span> 
        <span class="sn">到店自取</span>
    </div>
        </header>

<div class="deit_box">
    

        <van-contact-card class="deit_main_box"
       
  type="edit"
  :name="currentContact.name"
  :tel="currentContact.tel"
  @click="onEdit"
/>

</div>
 
 <van-popup v-model="show" position="top" :style="{ height: '60%' }" />
<van-cell  is-link @click="showPopup" v-model="carmodel"><span> 新建收件人 </span></van-cell>
<van-nav-bar title="地址信息" left-text="返回" left-arrow @click-left="onClickLeft"/>
<van-popup
    v-model="show"
    position="bottom"
    :style="{ height: '100%' }">
    
   <van-address-edit
  :area-list="areaList"
  show-postal
  show-delete
  show-set-default
  show-search-result
  save-button-text
  :search-result="searchResult"
  :area-columns-placeholder="['请选择', '请选择', '请选择']"
  @save="onSave"
  @delete="onDelete"
  @change-detail="onChangeDetail"
/>

</van-popup>

    
    </div>
</template>
<script>
import areaList from '@/area/areaList';
import { Toast } from 'vant';
export default {
    data() {
        return {
              currentContact: {
        name: 'Mr.Zhao',
        tel: '18277****0772',
      },
            areaList,
            show:false,
            carmodel:'',
            confirm:[],
        }
    },
  created(){
    this.$axios.post('http://47.115.51.185/api/order/confirm').then(d=>{
        this.confirm=d.data.data;
        console.log('结算列表')
        console.log(d)
    })
  },

    methods:{
        onClickLeft(){
             this.$router.push("/Paymentlist");
        },
         onEdit() {
      Toast('编辑');
    },
        // 弹出层显示
        onSave() {
      Toast('保存成功');
       this.$router.push("/Paymentlist");
    },
    onDelete(){
        this.$router.push("/Paymentlist");
    },
    onChangeDetail(){},
    searchResult(){},
        showPopup() {
            this.show = true;
            
            
        },
        //value=0改变省，1改变市，2改变区
        onChange(picker,){
            let val = picker.getValues();
            console.log(val)//查看打印
            let areaName = ''
            for (var i = 0; i < val.length; i++) {
                areaName = areaName+(i==0?'':'/')+val[i].name
            }
            this.carmodel = areaName
        },
        //确定选择城市
        onConfirm(val){
            console.log(val[0].name+","+val[1].name)
            this.show = false//关闭弹框
        },
        //取消选中城市
        onCancel(){
             this.show = false
             this.$refs.myArea.reset()// 重置城市列表
        },
       

    }
}
</script>
<style lang="less" scoped>
header{
    display: flex;
    // background-color:rgb(234,57,41);
     background-image: linear-gradient(#ee5757, #c28181);
}
.deit_box{
 display: flex;
 height: 50px;
 
}
.deit_main_span{
  
    
    display: flex;
    width: 96%;
    margin: 0 auto;
    height: 50px;
   
    .sn{
         margin-top: 15px;
        flex-wrap: wrap-reverse;
        width: 50%;
        background-color: rgb(247,193,189);
        text-align: center;
        line-height: 35px;

    }
    .sn-title{
        margin-top: 10px;
        background-color: white;
    }
}

.deit_main_box{
    // margin-top: 15px;

}
.cell{

    text-align: center  !important;
}

</style>